import {
    Host,
    Event,
    Component,
    h,
    Prop,
    EventEmitter,
    Element,
} from '@stencil/core';
import { classlist } from '../../utils/classlist';

const layoutVariantsConfig = {
    variants: {
        layout: {
            column: 'flex flex-col',
            row: 'flex',
            grid: 'grid grid-cols-3',
        },
    },
};

const getLayoutClass = classlist('gap-4', layoutVariantsConfig);

export interface RadioOption {
    option_id: string;
    label?: string;
    hint_text?: string;
    disabled?: boolean;
    readonly?: boolean;
    group?: string;
}

export type RadioLayout = 'column' | 'row' | 'grid';

/**
 * @category Form Controls
 * @description A radio button group component for selecting a single option from a predefined list. Provides grouped validation, labeling, and accessibility features for radio button collections.
 *
 * @slot - Used to place atui-radio elements when 'options' is not set
 */
@Component({
    tag: 'at-radio-group',
    shadow: false,
})
export class AtRadioGroup {
    /**
     * Label of the radio group.
     */
    @Prop()
    label?: string;

    /**
     * Optional info icon with detailed tooltip description.
     * Displayed at right of label.
     */
    @Prop()
    info_text?: string;

    /**
     * Direction that the radio elements are placed
     */
    @Prop()
    layout?: RadioLayout = 'column';

    /**
     * Disables the radio options but keeps the text readable. Use for global readonly form state.
     */
    @Prop()
    disabled?: boolean;

    /**
     * Disables interaction with atui-radio elements when set
     */
    @Prop()
    readonly?: boolean;

    /**
     * Adds a required * to the label
     */
    @Prop()
    required?: boolean;

    /**
     * Short description or validation hint if required
     */
    @Prop()
    hint_text?: string;

    /**
     * Shown when 'invalid' prop is set
     */
    @Prop()
    error_text?: string;

    /**
     * Shows the error text
     */
    @Prop()
    invalid: boolean;

    /**
     * Options displayed in the radio group.
     */
    @Prop()
    options: RadioOption[];

    /**
     * A string containing the Id of the currently selected radio
     */
    @Prop() value: string;

    @Element() el: HTMLAtRadioGroupElement;

    private radioEls: HTMLAtRadioElement[] = [];

    private radioGroupId = `radiogroup-${Math.random().toString(36).substring(2, 11)}`;

    /**
     * Emits an event when active radio element changes. `event.detail` is the ID of the active radio
     */
    @Event()
    atuiChange: EventEmitter<string>;

    handleChange(optionId: string): void {
        if (this.value !== optionId) {
            this.value = optionId;
            this.atuiChange.emit(optionId);
        }
    }

    focusAndClickRelativeRadio(relativePosition: number) {
        const indexOfActiveRadio = this.radioEls.indexOf(
            this.el.ownerDocument.activeElement as HTMLAtRadioElement,
        );
        const nextRadioEl =
            this.radioEls[
                (indexOfActiveRadio + relativePosition + this.radioEls.length) %
                    this.radioEls.length
            ];
        nextRadioEl.click();
        nextRadioEl.focus();
    }

    handleKeyDown(event: KeyboardEvent) {
        switch (event.key) {
            case 'ArrowRight':
            case 'ArrowDown':
                event.preventDefault();
                this.focusAndClickRelativeRadio(1);
                break;
            case 'ArrowUp':
            case 'ArrowLeft':
                event.preventDefault();
                this.focusAndClickRelativeRadio(-1);
                break;
        }
    }

    get getRadios(): any {
        if (this.options && this.options.length > 0) {
            return this.options.map((option) => (
                <at-radio
                    label={option.label}
                    hint_text={option.hint_text}
                    option_id={option.option_id}
                    readonly={this.readonly || option.readonly}
                    disabled={this.disabled || option.disabled}
                    value={this.value === option.option_id}
                    onAtuiChange={(event) => {
                        event.stopPropagation();
                        if (!this.disabled && !this.readonly) {
                            this.handleChange(option.option_id);
                        }
                    }}
                    group={this.label || this.radioGroupId}
                    ref={(el) => this.radioEls.push(el)}
                />
            ));
        }
    }

    render() {
        const classname = getLayoutClass({
            layout: this.layout,
        });
        return (
            <Host
                role="radiogroup"
                onKeyDown={(event) => this.handleKeyDown(event)}
                class="block w-full"
            >
                <div class="flex flex-col">
                    <slot name="label" />
                    {(this.label || this.required || this.info_text) && (
                        <at-form-label
                            label={this.label}
                            required={this.required}
                            info_text={this.info_text}
                        />
                    )}
                    {this.hint_text && (
                        <span
                            class="text-light mb-8 inline-block text-xs leading-tight"
                            data-name="radio-group-hint"
                        >
                            {this.hint_text}
                        </span>
                    )}
                </div>

                <ul
                    class={classname}
                    data-name="radio-group-options"
                >
                    <slot />
                    {this.getRadios &&
                        this.getRadios.map((radio) => (
                            <li class="flex">{radio}</li>
                        ))}
                </ul>

                {this.error_text && this.invalid && (
                    <span
                        class="text-error text-sm"
                        data-name="radio-group-error-text"
                    >
                        {this.error_text}
                    </span>
                )}
            </Host>
        );
    }
}
